import { Loader } from './modules/loader.js';
import { AudioController } from './modules/audio-controller.js';
import { WelcomeMessage } from './modules/welcome-message.js';
import { flyingStarsConfig } from './modules/particles-config.js';
import { initModal } from './modules/modal.js';
import { EasterEgg } from './modules/easter-egg.js';
import ParallaxBackground from './modules/parallax';

document.addEventListener('DOMContentLoaded', () => {
    const loader = new Loader();
    
    loader.loadAll()
        .then(() => {
            setTimeout(() => {
                loader.hide();
                initializeApp();
            }, 1000);
        })
        .catch(error => {
            console.error('Error loading assets:', error);
            loader.hide();
            initializeApp();
        });

    function initializeApp() {
        const audioController = new AudioController();
        const easterEgg = new EasterEgg(); // Initialize easter egg
        
        // Initialize particles
        particlesJS('flyingStars', flyingStarsConfig);
        // Initialize parallax
        new ParallaxBackground(); 

        // Handle layer transition
        const flowerButton = document.getElementById('flowerButton');
        // const mainContent = document.getElementById('mainContent'); // TODO: add pashal
        
        flowerButton.addEventListener('click', () => {
            // mainContent.classList.remove('hidden');
            setTimeout(() => {
                // mainContent.classList.add('visible');
            }, 50);
        });

        initModal();
    }
}); 