export class WelcomeMessage {
    constructor() {
        this.messageElement = document.getElementById('message');
    }

    display() {
        const nameParam = new URLSearchParams(window.location.search).get('name');
        let messageText = '';

        if (nameParam) {
            const names = nameParam.split(',');
            if (names.length > 2) {
                const lastName = names.pop();

                messageText = `Шаноўныя ${names.join(', ')} i ${lastName}!\nЗ вялікай радасцю запрашаем вас на нашае вяселле 18 траўня!\nКаб даць пачатак чароўнай цырымоніі, націсніце…`;
            } else if (names.length === 2) {
                messageText = `Шаноўныя ${names[0]} i ${names[1]}!\nЗ вялікай радасцю запрашаем вас на нашае вяселле 18 траўня!\nКаб даць пачатак чароўнай цырымоніі, націсніце…`;
            }
            else {
                messageText = `Прывітанне ${names[0]}!\nЗ вялікай радасцю запрашаем цябе на нашае вяселле 18 траўня!\nКаб даць пачатак чароўнай цырымоніі, націсніце…`;
            }
        } else {
            messageText = 'Прывітанне';
        }

        // Wrap each word or phrase in a span element
         const sentance = messageText.split('\n');
         const wrappedWords = sentance.map(sentence => {
            return `<p class="ml3">${sentence.split(' ').map(word => {
                return `<span class='word'>${word.split('').map(letter => `<span class='letter'>${letter}</span>`).join('')}</span>`;
            }).join(' ')}</p>`;
         });

        this.messageElement.innerHTML = `<span class="ml3">${wrappedWords.join(' ')}</span>`;

        this.animate();
    }

    animate() {
        anime.timeline({loop: false, complete: (anim) => {
            const button = document.createElement('button');
            button.type = 'button';
            button.textContent = 'СЮДЫ';
            button.title = 'сюды';
            button.classList.add('message-button');
            
            // Add click handler for star effect
            button.addEventListener('click', (e) => {
                e.preventDefault();
            

                function createStar() {
                    const star = document.createElement('div');
                    star.classList.add('star');
                    
                    // Randomize star position and size
                    star.style.left = `${Math.random() * window.innerWidth}px`;
                    const size = 8 + Math.random() * 12;
                    const duration = 2 + Math.random() * 3;
                    
                    star.style.fontSize = `${size}px`;
                    star.style.animationDuration = `${duration}s`;
                    
                    document.body.appendChild(star);
                    
                    // Remove star after animation completes
                    setTimeout(() => {
                        if (document.body.contains(star)) {
                            document.body.removeChild(star);
                        }
                    }, duration * 1000);
                }
                
                // Create stars at regular intervals
                const starInterval = setInterval(createStar, 20);
                  

                setTimeout(() => {
                    window.open(
                        'https://docs.google.com/forms/d/1OdO48KtC-nQwa8QJsFPHxeI7QAb-fJG39HiDiQkktlg/viewform?edit_requested=true',
                        '_blank',
                        'noopener,noreferrer'
                    );
                }, 5500);
            });

            this.messageElement.appendChild(button);
          }})
            .add({
                targets: '.ml3 .word .letter',
                opacity: [0,1],
                easing: "easeInOutQuad",
                duration: 2650,
                delay: (el, i) => 220 * (i+1)
            });
    }
} 